/* LoginForm.css */

.loginform {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
  }

  .loginform h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }

  .loginform ul {
    list-style: none;
    padding: 0;
    margin-bottom: 10px;
    color: red;
  }

  .loginform label {
    display: block;
    margin-bottom: 10px;
    font-weight: bold;
    color: #333;
  }

  .loginform input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid #ccc;
  }

  .loginform .input-parent {
    margin-bottom: 15px;
  }

/* LoginForm.css */

/* ... existing styles ... */

.loginform .button {
  display: block;
  width: fit-content; /* Adjust width to fit the content */
  padding: 10px 20px; /* Add padding for better button appearance */
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  background-color: #888; /* Change background color to gray (#888) */
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin: 10px auto; /* Center the button horizontally and add some margin */
}

.loginform .button:hover {
  background-color: #666; /* Darken the background color on hover */
}

